.header__link {
  text-decoration: none;
  cursor: pointer;
  color: #fff;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  text-align: right;
}

.header__link:hover {
  text-decoration: underline;
  cursor: pointer;
}

@media screen and (max-width: 400px) {
  .header__link{
    font-size: 14px;
    line-height: 17px;
  }
}

